import {Link} from "react-router-dom";
import moment from 'moment';

function BlogList({title, blogs}) {
    return (
        <div>
            <h2 className="blog-list-title">{title}</h2>
            {blogs.map((blog) => (
                <div className="blog-preview" key={blog.id}>
                    <Link to={`/blog/${blog.id}`}>
                    <h2>{blog.title}</h2>
                    <p className="blog-author">Written by {blog.author.first_name} {blog.author.last_name}, Published at { moment(blog.published).format('YYYY-MM-DD HH:MM:SS') }</p>
                    <p className="blog-excerpt">{blog.excerpt }</p>
                    </Link>
                </div>
            ))}
        </div>
    );
}

export default BlogList;