import {useState} from "react";
import {useHistory} from 'react-router-dom';
import axiosInstance from "../Axios";


function Create(){
    const dateTime = new Date();
    dateTime.setHours( dateTime.getHours() + 8 );
    const [title, setTitle] = useState('');
    const [excerpt, setExcerpt] = useState('');
    const [content, setContent] = useState('');
    const [published, setPublished] = useState(dateTime.toISOString().slice(0, -8));
    const [status, setStatus] = useState('published');
    const [category, setCategory] = useState('1');
    const [author, setAuthor] = useState('1');
    const [isPending, setIsPending] = useState(false);
    const history = useHistory();

    const handleSubmit = (e) => {
        e.preventDefault();
        const blog = {title, excerpt, content, published, status, category, author};
        // console.log(blog);
        setIsPending(true);

        fetch(axiosInstance.defaults.baseURL + 'blog_api/blogs/create/', {
            method: 'POST',
            headers:  {
            'Content-Type': 'application/json',
            'Authorization': axiosInstance.defaults.headers.Authorization
            },
            body: JSON.stringify(blog)
        }).then(() => {
            setIsPending(false);
            // history.go(-1);
            history.push('/');
        })
    }
    return (
        <div className="create">
            <h2>Add a New Blog</h2>
            <form onSubmit={handleSubmit}>
                <label>Blog Title:</label>
                <input type="text"
                       required
                       value={title}
                       onChange={(e) => setTitle(e.target.value)}
                />
                <label>Blog Excerpt:</label>
                <textarea
                rows="3"
                required
                value={excerpt}
                onChange={(e) => setExcerpt(e.target.value)}
                />
                <label>Blog Content:</label>
                <textarea
                rows="10"
                required
                value={content}
                onChange={(e) => setContent(e.target.value)}
                />
                <label>Blog Published:</label>
                <input
                type="datetime-local"
                required
                value={published}
                onChange={(e) => setPublished(e.target.value)}
                />
                <label>Blog Status:</label>
                <select
                    value={status}
                    onChange={(e) => setStatus(e.target.value)}
                >
                    <option value="published">Published</option>
                    <option value="draft">Draft</option>
                </select>
                <label>Blog Category:</label>
                <select
                    value={category}
                    onChange={(e) => setCategory(e.target.value)} 
                >
                    <option value="1">Literature</option>
                    <option value="2">WebDev</option>
                </select>
                <label>Blog Author:</label>
                <select
                    value={author}
                    onChange={(e) => setAuthor(e.target.value)}
                >
                    <option value="1">Leonhard Euler</option>
                    <option value="2">Isaac Newton</option>
                    <option value="3">Carl Gauss</option>
                    <option value="4">Gottfried Leibniz</option>
                    <option value="5">Joseph-Louis Lagrange</option>
                    <option value="6">Rene Decartes</option>                    
                </select>
                { !isPending && <button>Submit</button>}
                { isPending && <button disabled>Adding Now . . .</button>}
            </form>
            {/*<h6>{published}</h6>*/}
        </div>
    );
}

export default Create;