import {useParams} from "react-router";
import useFetch from "./useFetch";
import {Link, useHistory} from "react-router-dom";
import axiosInstance from "../Axios";

function BlogDetail() {
    const {id} = useParams();
    const { data:blog, error, isPending } = useFetch(axiosInstance.defaults.baseURL + 'blog_api/blog/' + id)
    const history = useHistory();

    const handleDelete = () => {
        fetch(axiosInstance.defaults.baseURL + 'blog_api/blog/' + blog.id, {
            method: 'DELETE',
                headers:  {
            'Content-Type': 'application/json',
            'Authorization': axiosInstance.defaults.headers.Authorization
            }
        }).then(() => {
            history.push('/');
        })
    }

    return (
       <div className="blog-detail">
           { isPending && <div>Loading . . .</div>}
           { error && <div>{ error }</div>}
           { blog && (
               <article>
               <h2>{ blog.title }</h2>
               <p>Written by <Link to={`/${blog.author}/blogs`}>{ blog.author.first_name } { blog.author.last_name }</Link></p>
               <div>{ blog.content }</div>
               <button onClick={handleDelete}>Delete</button>
               </article>
           )}
       </div>
    );
}
export default BlogDetail;