import BlogList from "./BlogList";
import useFetch from "./useFetch";
import axiosInstance from "../Axios";

function Home() {
    const {data: blogs, isPending, error} = useFetch(axiosInstance.defaults.baseURL + 'blog_api/blogs/');

    return (
        <div className="home">
            { error && <div>{error}</div>}
            { isPending && <div>Loading . . .</div>}
            {blogs && <BlogList blogs={blogs} title="All Blogs" />}
        </div>
    );
}

export default Home